@import url(https://fonts.googleapis.com/css?family=Lato|Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* font-family: 'Lato', sans-serif;
font-family: 'Raleway', sans-serif; */

html {
  font-size: 8px;
  background-color: #F5F5F5;
}

body {
  margin: auto;
  background-color: #F5F5F5;
}

h1 {
  font-family: 'Lato', sans-serif;
  font-size: 5.5rem;
  font-weight: 300;
  color: #2E2424;
  text-shadow: #A7A7A7 1px 3px 2px;
  margin-top: 0px;
}

h2 {
  font-family: 'Lato', sans-serif;
  font-size: 2.5rem;
  font-weight: 300;
  color: #80C79E;
  margin-bottom: 0px;
}

h3 {
  font-family: 'Lato', sans-serif;
  font-size: 1.5rem;
  font-weight: 500;
  color: #2E2424;
  margin-bottom: 0px;
}

.hidden {
  display: none;
}


@media screen and (min-width: 768px) {
  html {
    font-size: 12px;
  }
}

@media screen and (min-width: 1366px) {
  html {
    font-size: 16px;
  }
}
header {
  height: 8vh;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin: auto;
}

@media screen and (min-width: 768px) {
  header {
    height: 8vh;
    width: 90%;
  }
}

@media screen and (min-width: 1366px) {
  header {
    height: 8vh;
    width: 80%;
  }
}
.logo {
  flex: 1 1;
  margin-left: 3vw;
  margin-top: 3vh;
}

.logo-text {
  color: #2E2424;
  margin: 0px;
  display: block;
  width: 4.5rem;
  text-align: right;
  border-bottom: 0.1rem solid #80C79E;
}

@media screen and (min-width: 768px) {
  .logo {
    margin-left: 5vw;
    margin-top: 2vh;
  }
}

@media screen and (min-width: 1366px) {
  .logo {
    margin-left: 8vw;
    margin-top: 1vh;
  }
}
span {
  border-top: .2rem solid #2E2424;
  display: block;
  width: 3.5rem;
  margin-bottom: 1rem;
}

.collapsible-menu {
  position: absolute;
  display: block;
  right: 0px;
  top: 8vh;
}

.burger-menu {
  cursor: pointer;
  margin-top: 3vh;
  margin-right: 3vw;
}

@media screen and (min-width: 768px) {
  .burger-menu {
    display: none;
  }

  .collapsible-menu {
    display: none;
  }
}

.collapsible-links {
  list-style-image: none;
  display: flex;
  flex-direction: column;
  background-color: #2E2424;
  width: 100vw;
  justify-content: center;
  padding-top: 1em;
  padding-bottom: 1em;
  border-top: .2rem solid #80C79E;
  text-align: center;
}

.collapsible-links > li > a {
  text-align: center;
  flex: 1 1;
  display: list-item;
  font-family: 'Lato', sanfs-serif;
  font-size: 2rem;
  font-weight: 300;
  color: #FFFFFF;
  margin-bottom: 3rem;
  width: 100vw;
} 
.nav-links {
  display: none;
  margin-top: 4vh;
}

.nav-links > li {
  flex: 1 1;
  display: list-item;
}

.nav-links > li > a {
  font-family: 'Lato', sans-serif;
  font-size: 1rem;
  font-weight: 300;
  color: #2E2424;
}


@media screen and (min-width: 768px) {
  .nav-links {
    list-style: none;
    list-style-image: none;
    display: flex;
    flex-wrap: wrap;
    flex: 1 1;
  }
}

#main-headline {
  width: 80vw;
  padding-bottom: 20vh;
  margin-left: 5vw;
}

@media screen and (min-width: 768px) {
  #main-headline {
    margin-left: 10vw;
  }
}

@media screen and (min-width: 1366px) {
  #main-headline {
    margin-left: 20vw;
  }
}
#about-me-section {
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

#greeting {
  font-size: 4rem;
}

#about-me-description {
  font-family: 'Raleway', sans-serif;
  font-size: 2rem;
  font-weight: 300;
  color: #2E2424;
  word-spacing: 1rem;
  line-height: 3rem;
}

#about-me-text {
  flex: 1 1;
  padding-right: 10vw;
  padding-left: 10vw;
  padding-top: 20vh; 
  padding-bottom: 20vh;
}

#about-me-img {
  flex: 1 1;
  width: 100vw;
  height: 100vh;
}

@media screen and (min-width: 1200px) {
  #about-me-img {
    flex: 1 1;
    width: 50vw;
  }

  #about-me-description {
    font-size: 1rem;
    font-weight: 300;
    word-spacing: 0.5rem;
    line-height: 2rem;
  }

  #about-me-text {
    padding-right: 10vw;
    padding-left: 10vw;
    padding-top: 20vh; 
    padding-bottom: 20vh;
  }

  #greeting {
    font-size: 2rem;
  }
}
